<template>
    <nav>
        <v-toolbar flat app>
            <v-app-bar-nav-icon @click="drawer = !drawer" class="grey--text">
                <v-icon>menu</v-icon>
            </v-app-bar-nav-icon>
            <v-toolbar-title class="font-weight-medium">{{ Title }}</v-toolbar-title>
            <v-spacer></v-spacer>
        </v-toolbar>
        <v-divider></v-divider>

        <v-navigation-drawer app v-model="drawer">
            <v-toolbar flat app>
                <v-toolbar-title class="font-weight-medium">Portfolio</v-toolbar-title>
                <v-spacer></v-spacer>
            </v-toolbar>

            <v-divider></v-divider>
            <v-list shaped>
                <v-list-item-group v-model="selectedItem" color="primary">
                    <v-list-item
                        v-for="(section, i) in sections"
                        :key="i"
                        @click="
                        $vuetify.goTo(i, 0, 0);
                        setTitle(text);
                        drawer = !drawer;"
                    >
                        <v-list-item-icon>
                            <v-icon v-text="section.icon"></v-icon>
                        </v-list-item-icon>

                        <v-list-item-content>
                            <v-list-item-title
                                v-text="section.text"
                                class="text-h8 font-weight-medium"
                            ></v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-item-group>
            </v-list>
        </v-navigation-drawer>
    </nav>
</template>

<script>
export default {
    data() {
        return {
            drawer: false,
            selectedItem: 0,
            Title: "Portfolio",
            sections: [
                { text: 'About Me', icon: 'person' },
                { text: 'Projects', icon: 'category' },
                { text: 'Contacts', icon: 'contact_page' },
            ],
            methods: {
                onDrawerClick(i, text) {
                    return i + text;
                },
                setTitle(value) {
                    this.Title = value;
                },
            },
        };
    },
}
</script>

